.btn {
  display: inline-flex;
  padding: 20px;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  cursor: pointer;
  border: 4px solid #ffffff;
  background-color: transparent;
  user-select: none;
  box-sizing: border-box;
}
.btn--header-bottom {
  padding: 18px;
  min-width: 234px;
  min-height: 61px;
}
@media (max-width: 1199px) {
  .btn--header-bottom {
    padding: 20px;
    width: 100%;
    min-width: auto;
    min-height: auto;
  }
