.social-network ul {
  display: flex;
  flex-wrap: wrap;
  gap: 11px;
  max-width: 200px;
  margin: 0;
  padding: 0;
  list-style-type: none;

  @include vp-1199 {
    max-width: 135px;
  }

  @include vp-767 {
  }
}

.social-network li {

  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: $color-default-white;
}

.social-network a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  font-size: 0;
  line-height: 0;
}
